import React, { useEffect, useState } from "react";
import { Row, Col, Input, Card, Pagination, Spin, Popconfirm, Switch } from "antd";
import { Table, Space } from "antd";
import Swal from "sweetalert2";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getRejectreasons,
  rejectreasonsEdit,
  rejectreasonsDelete,
  isModelVisible,
  SetRejectreasonsParams,
} from "../../../../store/Masters/Rejectreasons/rejectreasonsSlice";
import AddRejectreasons from "./addRejectreasons";

function Rejectreasons(props) {
  const { Search } = Input;
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  useEffect(() => {
    props.getRejectreasons({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "10%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "25%",
    },
    {
      title: "Reject Reason",
      dataIndex: "name",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "25%",
    },
    {
      title: "Reject Type",
      dataIndex: "type_name",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "25%",
    },
    {
      title: "Editable",
      render: (row) => (
        <span className={columnClass}>{row.editable? 'Yes' : 'No'}</span>
      ),
      width: "25%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_rejectreason") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.rejectreasonsEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_rejectreason") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.rejectreasonsDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.rejectreasonslist.map((row, key) => ({
    ...row,
    index:
      (props.rejectreasonsParams.page - 1) *
        props.rejectreasonsParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetRejectreasonsParams({
      ...props.rejectreasonsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getRejectreasons({
      ...props.rejectreasonsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetRejectreasonsParams({ ...props.rejectreasonsParams, search: val });
    props.getRejectreasons({ ...props.rejectreasonsParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }

  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Reject Reasons Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_rejectreason") !== -1 &&
          "Add Reject Reason"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-10">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getRejectreasons({
                      ...props.rejectreasonsParams,
                      page: 1,
                      search: "",
                    });
                    props.SetRejectreasonsParams({
                      ...props.rejectreasonsParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.rejectreasonsParams?.page}
                pageSize={props.rejectreasonsParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddRejectreasons />
    </Row>
  );
}

function mapStateToProps({ rejectreasons,system }) {
  return {
    rejectreasonslist: rejectreasons.rejectreasonslist,
    noofpages: rejectreasons.noofpages,
    listCount: rejectreasons.listCount,
    rejectreasonsParams: rejectreasons.rejectreasonsParams,
    loading: rejectreasons.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getRejectreasons,
  rejectreasonsEdit,
  rejectreasonsDelete,
  isModelVisible,
  SetRejectreasonsParams,
})(Rejectreasons);
