import { Fragment, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Select, Row, Col, Switch } from "antd";
import {
  rejectreasonsAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Rejectreasons/rejectreasonsSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddRejectreasons = (props) => {
  const [form] = Form.useForm();
  const rejectReasonType = [
    { id: 1, name: "Purchase" },
    { id: 2, name: "Sale" },
    { id: 3, name: "Both" },
  ];
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.selectedData?.name,
      type: props.selectedData?.type,
      editable: props.selectedData?.editable
    };
    if (obj.name != null || obj.name == "") {
      props.rejectreasonsAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const countryToUpdate = {
      id: props.selectedData.id,
      name: props.selectedData?.name,
      type: props.selectedData?.type,
      editable: props.selectedData?.editable
    };
    props.rejectreasonsAdd(countryToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Reject Reason
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name,
      type: props.selectedData.type,
      editable: props.selectedData.editable
    });
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      {" "}
      <Form
        layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        {" "}
        <Row gutter={[12, 12]}>
          <Col span={24} xs={24} xl={24} md={24}>
            <Form.Item
              label="Reason Type"
              name="type"
              rules={[
                {
                  required: true,
                  message: "Please Select Reason Type",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Reason Type"
                optionFilterProp="type"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "type",
                    value: value,
                  });
                }}
                value={props.selectedData?.type}
              >
                {rejectReasonType.map((reasonType, index) => {
                  return (
                    <Option key={index} value={reasonType.id}>
                      {reasonType.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={24} md={24}>
            <Form.Item
              label="Reject Reason Name :"
              name="name"
              rules={[
                {
                  required: true,
                  message: "Please input your Reject Reason Name!",
                },
                {
                  pattern: new RegExp(
                    /^[a-zA-Z][a-zA-Z\s]+$/i
                  ),
                  message: "field does not accept numbers",
                }
              ]}
              hasFeedback
            >
              <Input
                id="Country"
                name="name"
                style={{ width: "100%" }}
                placeholder="Reject Reason Name"
                value={props.selectedData?.name}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col span={24} xs={24} xl={24} md={24}>
            <Form.Item
              label="Editable"
              name="editable"
              rules={[
                {
                  required: true,
                  message: "Please Select Editable",
                },
              ]}
              hasFeedback
            >
              {" "}
              <Switch
                checkedChildren="On"
                unCheckedChildren="Off"
                // defaultChecked finalapproval
                checked={props.selectedData.editable}
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "editable",
                    value: value,
                  });
                }}
              />
              </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ rejectreasons }) {
  return {
    selectedData: rejectreasons.rowdata,
    model: rejectreasons.model,
  };
}

export default connect(mapStateToProps, {
  rejectreasonsAdd,
  isModelVisible,
  InputChangeValue,
})(AddRejectreasons);
